.popup__container {
  max-width: 430px;
  min-width: 282px;
  height: min(330px, 100%);
  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  position: relative;
}

@media screen and (max-width: 526px) {
  .popup__container {
    width: calc(100% - 38px);
    height: min(322px, 100%);
  }
}
